import React, { Fragment } from 'react';
import { useTranslation } from 'react-i18next';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Hidden from '@material-ui/core/Hidden';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import { Link } from 'react-scroll';

import constants from '../../config/constants';
import ListDescription from '../listDescription';
import useStyles from './homeStyles';

export interface IHomeState {
  loading: boolean;
}

export interface IHomeProps {
  loading: boolean;

  onSubmit: (value: boolean) => () => void;
}

const Home = (): JSX.Element => {
  const { t } = useTranslation();
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <CssBaseline />
      <main className={classes.content}>
        {constants.firms.map(firm => {
          return (
            <div key={firm.title} id={firm.title}>
              <ListDescription
                title={t(firm.title)}
                description={t(firm.description)}
                period={t(firm.period)}
              />
              <Typography paragraph>
                <strong>{t('learnedTechnology')}</strong>
              </Typography>
              <List>
                {firm.technologies.map(technology => (
                  <Fragment key={technology}>
                    <ListItem>
                      <div className={classes.listPoint}></div>
                      <ListItemText primary={technology} className={classes.listText} />
                    </ListItem>
                  </Fragment>
                ))}
              </List>
              <Divider />
            </div>
          );
        })}
      </main>
      <Hidden mdDown={true}>
        <Drawer
          className={classes.drawer}
          variant="permanent"
          classes={{
            paper: classes.drawerPaper,
          }}
          anchor="right"
        >
          <div className={classes.toolbar} />
          <List>
            {constants.firms.map(firm => {
              return (
                <Link
                  to={`${firm.title}`}
                  key={firm.title}
                  activeClass={classes.active}
                  offset={1}
                  duration={500}
                  spy={true}
                  hashSpy={true}
                  smooth={true}
                  className={classes.link}
                >
                  <ListItem button>
                    <ListItemText
                      primary={t(firm.title)}
                      classes={{ primary: classes.anchors }}
                    />
                  </ListItem>
                </Link>
              );
            })}
          </List>
        </Drawer>
      </Hidden>
    </div>
  );
};

export default Home;
