import React, { useState, useEffect } from 'react';
import { Link, NavLink } from 'react-router-dom';
import clsx from 'clsx';
import { useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import Drawer from '@material-ui/core/Drawer';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import MenuIcon from '@material-ui/icons/Menu';
import Divider from '@material-ui/core/Divider';
import { useTranslation } from 'react-i18next';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import PortraitIcon from '@material-ui/icons/EmojiEvents';
import EmojiEventsIcon from '@material-ui/icons/Portrait';
import ContactPhoneIcon from '@material-ui/icons/ContactPhone';
import SportsTennisIcon from '@material-ui/icons/SportsTennis';

import keyboard from '../../assets/img/keyboard.png';
import keyboard1200 from '../../assets/img/keyboard1200.png';
import keyboard750 from '../../assets/img/keyboard500.png';
import steak from '../../assets/img/steak.png';
import steak1200 from '../../assets/img/steak1200.png';
import steak750 from '../../assets/img/steak500.png';
import contacts from '../../assets/img/contacts.png';
import contacts1200 from '../../assets/img/contacts1200.png';
import contacts750 from '../../assets/img/contacts500.png';
import hobby from '../../assets/img/hobby.jpg';
import hobby1200 from '../../assets/img/hobby1200.jpg';
import hobby750 from '../../assets/img/hobby500.jpg';
import FlagIcon from '../../flagIcon';
import useStyles from './headerStyles';
import './styles.css';

const imageList: any = {
  homePage: {
    v1920: keyboard,
    v1200: keyboard1200,
    v750: keyboard750,
  },
  technologyPage: {
    v1920: steak,
    v1200: steak1200,
    v750: steak750,
  },
  contactPage: {
    v1920: contacts,
    v1200: contacts1200,
    v750: contacts750,
  },
  interestingPage: {
    v1920: hobby,
    v1200: hobby1200,
    v750: hobby750,
  },
};

const getImageClass = (url: string): string => {
  switch (url) {
    case '/':
      return 'homePage';
    case '/technology':
      return 'technologyPage';
    case '/interesting':
      return 'interestingPage';
    case '/contacts':
      return 'contactPage';
    default:
      return 'homePage';
  }
};

const Header = ({ path }: { path: string }): JSX.Element => {
  const { i18n, t } = useTranslation();
  const classes = useStyles();
  const menuId = 'primary-search-account-menu';
  const [anchorEl, setAnchorEl] = useState(null);
  const [leng, setLeng] = useState(i18n.language.length > 2 ? 'ru' : i18n.language);
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);
  const [isHeaderImageLoading, setIsHeaderImageLoading] = useState(true);
  const isMenuOpen = Boolean(anchorEl);

  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };
  const handleProfileMenuOpen = (e: any) => {
    setAnchorEl(e.currentTarget);
  };
  const handleMenuClose = (lng: string) => () => {
    setLeng(lng);
    setAnchorEl(null);
  };

  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handleMenuClose('gb')}>English</MenuItem>
      <MenuItem onClick={handleMenuClose('ru')}>Русский</MenuItem>
      <MenuItem onClick={handleMenuClose('by')}>Беларусская</MenuItem>
    </Menu>
  );

  useEffect((): void=>{
    i18n.changeLanguage(leng);
  }, [i18n, leng])

  return (
    <>
      <div className={classes.grow}>
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              edge="start"
              className={clsx(classes.menuButton, open && classes.hide)}
            >
              <MenuIcon />
            </IconButton>
            <Typography className={classes.title} variant="h6" noWrap>
              {t(path)}
            </Typography>
            <div className={classes.grow} />
            <div className={classes.sectionDesktop}>
              <IconButton
                edge="end"
                aria-label="account of current user"
                aria-controls={menuId}
                aria-haspopup="true"
                onClick={handleProfileMenuOpen}
                color="inherit"
              >
                <FlagIcon code={leng} size={'lg'} />
              </IconButton>
            </div>
            <div className={classes.sectionMobile}>
              <IconButton
                edge="end"
                aria-label="account of current user"
                aria-controls={menuId}
                aria-haspopup="true"
                onClick={handleProfileMenuOpen}
                color="inherit"
              >
                <FlagIcon code={leng} size={'lg'} />
              </IconButton>
            </div>
          </Toolbar>
        </AppBar>
        {renderMenu}
        <Drawer
          className={classes.drawer}
          variant="persistent"
          anchor="left"
          open={open}
          classes={{
            paper: classes.drawerPaper,
          }}
        >
          <div className={classes.drawerHeader}>
            <IconButton onClick={handleDrawerClose}>
              {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
            </IconButton>
          </div>
          <Divider />
          <List>
            <Link className={clsx(classes.menuLink, '/' === path && classes.active)} to="/">
              <ListItem button>
                <ListItemIcon>
                  <EmojiEventsIcon />
                </ListItemIcon>
                <ListItemText primary={t('home')} />
              </ListItem>
            </Link>
            <NavLink
              className={classes.menuLink}
              activeClassName={classes.active}
              to="/interesting"
            >
              <ListItem button>
                <ListItemIcon>
                  <SportsTennisIcon />
                </ListItemIcon>
                <ListItemText primary={t('interesting')} />
              </ListItem>
            </NavLink>
            <NavLink
              className={classes.menuLink}
              activeClassName={classes.active}
              to="/technology"
            >
              <ListItem button>
                <ListItemIcon>
                  <PortraitIcon />
                </ListItemIcon>
                <ListItemText primary={t('technology')} />
              </ListItem>
            </NavLink>
            <NavLink
              className={classes.menuLink}
              activeClassName={classes.active}
              to="/contacts"
            >
              <ListItem button>
                <ListItemIcon>
                  <ContactPhoneIcon />
                </ListItemIcon>
                <ListItemText primary={t('contacts')} />
              </ListItem>
            </NavLink>
          </List>
        </Drawer>
      </div>
      <div className={clsx('header', getImageClass(path))}>
        <div
          className={clsx('headerImBlock', getImageClass(path), {
            headerBlur: isHeaderImageLoading,
          })}
        />
        {isHeaderImageLoading && <div className="smallHeaderImage headerBlur" />}
        <picture className="headerImageContainer">
          <source media="(max-width: 750px)" srcSet={imageList[getImageClass(path)].v750} />
          <source media="(max-width: 1200px)" srcSet={imageList[getImageClass(path)].v1200} />
          <img
            src={imageList[getImageClass(path)].v1920}
            alt=""
            onLoad={() => {
              setTimeout(() => {
                setIsHeaderImageLoading(false);
              }, 200);
            }}
          />
        </picture>
      </div>
    </>
  );
};

export default Header;
