import { IHomeState } from '../../components/home/homeComponent';

export const initialState: IHomeState = {
  loading: false,
};

const home = (state: IHomeState = initialState, action: any): any => {
  if (action.type === 'Home') {
    const { loading } = action.payload;
    return {
      ...state,
      loading,
    };
  } else {
    return {
      ...state,
    };
  }
};

export default home;
