import React from 'react';
import { useTranslation } from 'react-i18next';
import Card from '@material-ui/core/Card';
import Grid from '@material-ui/core/Grid';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';

import constants from '../../config/constants';
import useStyles from './interestingStyles';
import history from '../../assets/img/history.png';
import games from '../../assets/img/broodwar.png';
import guitare from '../../assets/img/guitare.png';
import literature from '../../assets/img/lit_re.png';
import programmer from '../../assets/img/programmer.png';
import sport from '../../assets/img/st_t.png';

const images: any = {
  literatureImage: literature,
  musicImage: guitare,
  sportImage: sport,
  gamesImage: games,
  developmentImage: programmer,
  historyImage: history,
};

const Interesting = (): JSX.Element => {
  const { t } = useTranslation();
  const classes = useStyles();
  return (
    <Grid container className={classes.root} spacing={1}>
      {constants.interesting.map(interests => (
        <Grid
          item
          lg={3}
          md={4}
          sm={6}
          xl={2}
          xs={12}
          key={interests.title}
          className={classes.cardContainer}
        >
          <Card className={classes.card}>
            <CardMedia
              component="img"
              alt="Contemplative Reptile"
              height="140"
              image={images[interests.image]}
              title="Contemplative Reptile"
            />
            <CardContent>
              <Typography gutterBottom variant="h5" component="h2">
                {t(interests.title)}
              </Typography>
              <Typography variant="body2" color="textSecondary" component="p">
                {t(interests.description)}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      ))}
    </Grid>
  );
};

export default Interesting;
