import React from 'react';
import { useTranslation } from 'react-i18next';
// import useStyles from './technologyStyles';
// import CssBaseline from '@material-ui/core/CssBaseline';

// import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { Icon } from '@iconify/react';
import reduxIcon from '@iconify/icons-simple-icons/redux';
import nodejsIcon from '@iconify/icons-simple-icons/nodejs';
import javaScriptIcon from '@iconify/icons-simple-icons/javascript';
import reactIcon from '@iconify/icons-simple-icons/react';
import graphqlIcon from '@iconify/icons-simple-icons/graphql';
import wordpressIcon from '@iconify/icons-simple-icons/wordpress';
import materialdesignIcon from '@iconify/icons-simple-icons/materialdesign';

import ListDescription from '../listDescription';
import constants from '../../config/constants';

const iconsList: any = {
  jsTitle: javaScriptIcon,
  reactTitle: reactIcon,
  reduxTitle: reduxIcon,
  matherialTitle: materialdesignIcon,
  nodeTitle: nodejsIcon,
  graphQLTitle: graphqlIcon,
  wordPressTitle: wordpressIcon,
};

function TabPanel({
  children,
  value,
  index,
  ...other
}: {
  children: JSX.Element;
  value: number;
  index: number;
}) {
  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-force-tabpanel-${index}`}
      aria-labelledby={`scrollable-force-tab-${index}`}
      {...other}
    >
      <Box p={3}>{children}</Box>
    </Typography>
  );
}

function a11yProps(index: number) {
  return {
    id: `scrollable-force-tab-${index}`,
    'aria-controls': `scrollable-force-tabpanel-${index}`,
  };
}

const Technology = (): JSX.Element => {
  // const classes = useStyles();
  const { t } = useTranslation();
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
  };

  return (
    <>
      <AppBar position="static" color="default">
        <Tabs
          value={value}
          onChange={handleChange}
          variant="scrollable"
          scrollButtons="on"
          indicatorColor="primary"
          textColor="primary"
          aria-label="scrollable force tabs example"
        >
          {constants.technologies.map((el, index) => (
            <Tab
              key={el.title}
              label={t(el.title)}
              icon={<Icon icon={iconsList[`${el.title}`]} />}
              {...a11yProps(index)}
            />
          ))}
        </Tabs>
      </AppBar>
      {constants.technologies.map((el, index) => (
        <TabPanel value={value} key={el.title} index={index}>
          <ListDescription
            title={t(el.title)}
            description={t(el.description)}
            period={t(el.period)}
          />
        </TabPanel>
      ))}
    </>
  );
};

export default Technology;
